/* custom */
/* src/App.css */
.project-thumbnail {
  width: 100%;
  height: 140px;
  object-fit: cover;
  border-radius: 4px;
}


/* Remove border line from navbar */
.MuiAppBar-root {
  box-shadow: none;
}

/* for footer */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1;
}

/* Navbar link styles */
.nav-link {
  color: inherit;
  text-decoration: none;
  border-radius: 4px;
  transition:
    background-color 0.3s,
    color 0.3s,
    font-weight 0.3s;
}

.nav-link.active {
  color: #1976d2 !important; /* primary.main color */
  background-color: rgba(25, 118, 210, 0.1); /* primary.light color */
  border-radius: 4px;
}

/* Responsive styles */
@media (max-width: 600px) {
  .nav-link {
    font-size: 14px;
  }

  .profile-picture {
    width: 100px;
    height: 100px;
  }

  .content-container {
    padding: 10px;
  }
}
